.c-designers-carousel {
  background: $s-color--background-focus;

  &__inner {
    padding: 3.375rem 0 4rem;
  }

  &__tabs {
    position: relative;
    display: flex;
    justify-content: center;
    width: max-content;
    margin: 0 auto;
    margin-bottom: 2.4375rem;

  }

  &__tab-blob {
    position: absolute;
    z-index: $s-z--reset;
    width: 9.625rem;
    transition: all .5s;

    &.is-first-active {
      left: 0%;
    }

    &.is-second-active {
      left: 50%;
    }
  }

  &__tab {
    height: 3.8125rem;
    z-index: $s-z--text-overlay;
    transition: color .3s;
    width: 9.625rem;
    text-align: center;
    color: $s-color--btn-font-alt;
    cursor: pointer;

    &.is-active {
      color: $s-color--btn-font;
    }
  }

  &__sliders{
   display: grid;
   grid-template-columns: 1fr;
   grid-template-rows: 1fr;
  }

  &__slider {
    opacity: 0;
    visibility: hidden;
    grid-row: 1/-1;
    grid-column: 1/-1;
    display: flex;
    justify-content: space-between;
    gap: 1.25rem;
    padding: 0 $s-gutter--edge-mobile;
    transition: opacity .5s;
    overflow-x: scroll;
    overflow-y: hidden;
    cursor: grab;
    user-select: none;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    &.is-visible {
      opacity: 1;
      visibility: visible;
    }

    &.is-centered{
      justify-content: center;
    }
  }

  &__slide {
    margin-bottom: 0;
    white-space: nowrap;
    scroll-snap-align: center;
  }


  @include t-media-query($s-medium-up) {
    &__inner{
      padding-top: 4.0625rem;
    }

    &__slider {
      padding: 0 min(5.75%, 7.1875rem);
      gap: 3.4125rem;
    }

    &__tabs{
      margin-bottom: 3.8125rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner{
      padding-top: 7.0625rem;
      padding-bottom: 9.8125rem;
      max-width: $s-page-wrap--xlarge;
      margin: 0 auto;
    }

    &__slider {
      gap: 5.6875rem;
    }

    &__tab-blob,
    &__tab {
      width:16.9375rem;
      height: 6.8125rem;
      font-size: 1.5rem;
    }
  }
}